<template>
  <v-layout
    wrap
  >
    <v-navigation-drawer
      v-model="drawer"
      absolute
      temporary
      right
      width="500"
    >
      <v-expansion-panel 
        v-for="(item, i) in keywords"
        :key="i"
      >
        <v-expansion-panel-content>
          <template v-slot:header>
            <div>{{item}}</div>
          </template>
          <v-card v-for="(fav, j) in favorites[i]" :key="j">
            <img :src="fav.imageURL" alt="image" class="image">
            <!-- <v-card-text>{{fav}}</v-card-text> -->
          </v-card>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-navigation-drawer>
  </v-layout>
</template>

<script>

import { EventBus } from '@/scripts/event-bus.js'
import { getFavorites } from '@/scripts/images-api'

export default {
  props: {
    keywords: Array,
    favorites: Array
  },
  data() {
    return {
      drawer: false,
      images: [],
      faves: []
    }
  },
  methods: {
    setDrawer() {
      this.drawer = !this.drawer

      if (this.drawer) {
        for (let i = 0; i < this.keywords.length; i++) {
           getFavorites(i, this.keywords[i], this.setFaves)
        }
      }
    },
    setFaves(i, data) {
      this.faves[i] = data
    }
  },
  mounted() {
    EventBus.$on('toggle-drawer', this.setDrawer)
  },
  updated() {
    for (let i = 0; i < this.keywords.length; i++) {
        getFavorites(i, this.keywords[i], this.setFaves)
    }
  }
}

</script>

<style scoped>

.image {
  max-width: 500px;
}

</style>
