<template>
  <v-app>
    <Toolbar :getImagesCallback="getImagesData" :getKeywordsCallback="setKeywords"/>
    <v-content>
      <v-parallax src="https://cdn.vuetifyjs.com/images/parallax/material.jpg" :height="height">
        <Images v-if="images" :images="images" :keyword="keyword" :getKeywordsCallback="setKeywords"/>
        <Welcome v-else/>
        <Drawer :keywords="keywords" :favorites="favorites"/>
      </v-parallax>
    </v-content>
  </v-app>
</template>

<script>
import Toolbar from '@/components/Toolbar'
import Welcome from '@/components/Welcome'
import Images from '@/components/Images'
import Drawer from '@/components/Drawer'

import { fetchImages, getFavorites } from '@/scripts/images-api'

export default {
  name: 'App',
  components: {
    Toolbar,
    Welcome,
    Images,
    Drawer
  },
  data() {
    return {
      height: window.innerHeight - 64,
      images: null,
      keyword: '',
      keywords: [],
      favorites: [],
      tempKeyword: ''
    }
  },
  methods: {
    getImagesData(keyword) {
      this.keyword = keyword
      fetchImages(keyword, this.setImages)
    },
    setImages(data) {
      this.images = data
    },
    setKeywords(data) {
      let temp = []

      for (let i = 0; i < data.length; i++) {
        temp.push(data[i].keyword)
      }

      this.keywords = temp

      for (let i = 0; i < this.keywords.length; i++) {
        getFavorites(i, this.keywords[i], this.setFavoriteImages)
      }
      this.$forceUpdate()
    },
    setFavoriteImages(i, data) {
      this.favorites[i] = data
    }
  },
  updated() {
    for (let i = 0; i < this.keywords.length; i++) {
      getFavorites(i, this.keywords[i], this.setFavoriteImages)
    }
  }
}
</script>
