import axios from 'axios'

export function fetchImages(keyword, callback) {
  axios({
    method: 'get',
    url: 'http://lest-cst336.herokuapp.com/labs/lab8/api/pixabayProxy.php',
    params: {
      keyword: keyword
    }
  })
  .then(response1 => {
    callback(response1.data)
  });
}

export function getFavorites(index, keyword, callback) {
  axios({
    method: 'get',
    url: 'http://lest-cst336.herokuapp.com/labs/lab8/api/favoritesAPI.php',
    params: {
      action: 'favorites',
      keyword: keyword
    }
  })
  .then(response1 => {
    callback(index, response1.data)
  });
}

export function addFavorite(keyword, imageURL, callback) {
  axios({
    method: 'get',
    url: 'http://lest-cst336.herokuapp.com/labs/lab8/api/favoritesAPI.php',
    params: {
      action: 'add',
      keyword: keyword,
      favorite: imageURL
    }
  })
  .then(response1 => {
    callback(response1)
  });
}

export function removeFavorite(imageURL, callback) {
  axios({
    method: 'get',
    url: 'http://lest-cst336.herokuapp.com/labs/lab8/api/favoritesAPI.php',
    params: {
      action: 'delete',
      favorite: imageURL
    }
  })
    .then(response1 => {
      callback(response1)
    });
}

export function getKeywords(callback) {
  axios({
    method: 'get',
    url: 'http://lest-cst336.herokuapp.com/labs/lab8/api/favoritesAPI.php',
    params: {
      action: 'keyword',
    }
  })
  .then(response1 => {
    callback(response1.data)
  });
}