<template>
  <v-layout align-center justify-center="">
    <v-flex sm5>
      <v-card>
        <v-container grid-list-sm fluid>
          <v-layout row wrap>
            <v-flex
              v-for="(image, index) in images"
              :key="index"
              xs4
              d-flex
            >
              <v-card flat tile class="d-flex">
                <v-hover>
                  <v-img
                    slot-scope="{hover}"
                    :src="`${image}`"
                    :lazy-src="`${image}`"
                    aspect-ratio="1"
                    class="grey lighten-2"
                    @mouseenter="setCurrentIndex(index)"
                    @mouseleave="setCurrentIndex(-1)"
                  >
                    <div
                      v-if="hover"
                      class="d-flex transition-fast-in-fast-out grey darken-4 v-card--reveal display-3"
                      style="height: 100%;"
                    >
                      <v-layout justify-center align-center fill-height>
                        <v-btn flat icon color="red" @click="toggleFavorite(image)" :id="index">
                          <v-icon v-if="favorited[index]==true">favorite</v-icon>
                          <v-icon v-else>favorite_border</v-icon>
                        </v-btn>
                      </v-layout>
                    </div>
                    <template v-slot:placeholder>
                      <v-layout
                        fill-height
                        align-center
                        justify-center
                        ma-0
                      >
                        <v-progress-circular indeterminate color="grey lighten-5"></v-progress-circular>
                      </v-layout>
                    </template>
                  </v-img>
                </v-hover>
              </v-card>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
import Heart from '@/components/Heart'
import HeartBorder from '@/components/HeartBorder'
import { getKeywords, addFavorite, removeFavorite } from '@/scripts/images-api'

export default {
  components: {
    Heart,
    HeartBorder
  },
  props: {
    keyword: String,
    images: Array,
    getKeywordsCallback: Function
  },
  data() {
    return {
      currentIndex: -1,
      favorited: {},
      firstClicked: {}
    }
  },
  methods: {
    setCurrentIndex(index) {
      this.currentIndex = index
    },
    toggleFavorite(imageURL) {
      // This is a hack to make the icons work
      if (!this.firstClicked[this.currentIndex]) {
        document.getElementById(this.currentIndex.toString()).click()
        this.firstClicked[this.currentIndex] = true

      }

      // This is fine
      if (this.favorited[this.currentIndex] == false) {
        this.favorited[this.currentIndex] = true
        addFavorite(this.keyword, imageURL, this.setFavorite)
      }
      else {
        this.favorited[this.currentIndex] = false
        removeFavorite(this.keyword, this.setFavorite)
      }

      getKeywords(this.getKeywordsCallback)

      // This is a hack to make the icons work
      this.$forceUpdate()
    },
    setFavorite(data) {
      console.log(data)
    }
  },
  mounted() {
    for (let i = 0; i < this.images.length; i++) {
      this.favorited[this.currentIndex] = false
    }
  }
}

</script>

<style scoped>

.v-card--reveal {
  align-items: center;
  bottom: 0;
  justify-content: center;
  opacity: .5;
  position: absolute;
  width: 100%;
}

</style>
