<template>
  <v-toolbar app>
    <v-toolbar-title class="headline text-uppercase">
      <span>Pixabay </span>
      <span class="font-weight-light">Image Search</span>
    </v-toolbar-title>
    <v-spacer></v-spacer>
    <v-text-field
      label="Keyword"
      color="gray darken-3"
      class="field"
      v-model="keyword"
      @keyup.enter="searchImages"
    ></v-text-field>
    <v-btn
      flat
      @click="searchImages"
    >
      <span>Search</span>
    </v-btn>
    <v-spacer></v-spacer>
    <v-btn
      flat
      @click="getKeywordsFavorites"
    >
      <span>View favorites</span>
    </v-btn>
  </v-toolbar>
</template>

<script>

import { getKeywords } from '@/scripts/images-api'
import { EventBus } from '@/scripts/event-bus.js';

export default {
  props: {
    getImagesCallback: Function,
    getKeywordsCallback: Function
  },
  data() {
    return {
      keyword: '',
      keywordsFavorites: null
    }
  },
  methods: {
    searchImages() {
      this.getImagesCallback(this.keyword)
    },
    getKeywordsFavorites() {
      EventBus.$emit('toggle-drawer')
      getKeywords(this.setKeywordsFavorites)
    },
    setKeywordsFavorites(data) {
      this.keywordsFavorites = data
      console.log(this.keywordsFavorites)
      this.getKeywordsCallback(this.keywordsFavorites)
    }
  },
  mounted() {
    this.getKeywordsFavorites()
  }
}

</script>

<style scoped>

.field {
  margin-bottom: -1em;
}

</style>
